
import Step from "../../../base/step/step.vue"
import Scroll from '../../../base/scroll/scroll.vue'
import Rater from 'vux/src/components/rater/index.vue'

export default {
name: "component_name",
components: {
Step,
Scroll,
Rater
},
data() {
return {
isOk: true,
starStyle: '', //
red: 'red', //
active: true, // or 
priceNum: 5, //
goods1: 5,
goods2: 5,
}
},
methods: {
doNext() {
let that = this;
this.$axios.post('/api/orderOperate/backOrderSubmit', {
buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
favorText: this.$route.query.commonMsg,
favorPicId: this.$route.query.commonImg,
evaluationRealityScore: this.goods1,
evaluationRichScore: this.goods2,
isRecommendOtherBuyer: this.active ? 1 : 0
}).then((data) => {
console.log(data)
if (data.data.code === '200') {
this.$router.push({ name: 'submitSuccess', query: { type: 'evaluate2' } })
} else {
this.$vux.alert.show({
title: '',
content: data.data.message,
})
}
}).catch((error) => {
this.$vux.alert.show({
title: '',
content: '',
})
})
}
}
}



